$text: #fff;
$regular-text: 17px;
$lineheight: 26px;
$userfont: UbuntuLight, sans-serif;

%sf-regular {
	font-family: "SFProDisplay-Regular";
	font-weight: 400;
}

%sf-semibold {
	font-family: "SFProDisplay-Semibold";
	font-weight: 600;
}

%ub-light {
	font-family: "UbuntuLight";
	font-weight: 300;
}

%ub-regular {
	font-family: "UbuntuRegular";
	font-weight: 400;
}

%ub-medium {
	font-family: "UbuntuMedium";
	font-weight: 500;
}

%ub-bold {
	font-family: "UbuntuBold";
	font-weight: 700;
}

$systemfont: -apple-system, BlinkMacSystemFont, Arial, sans-serif;
$transition: .5s cubic-bezier(.42, .61, .58, .41);

// Container & Breakpoints
$containerWidth: 1240 + px;
$containerPadding: 15 + px;

