.contacts {
	&__inner {
		display: flex;
	}

	&__info {
		width: 240px;
		margin-right: 60px;
		margin-left: 50px;
	}

	&__info-box {
		margin-bottom: 30px;
		margin-right: 15px;
	}

	&__info-title {
		margin-bottom: 13px;
		font-size: 21px;
		line-height: 24px;
		@extend %ub-bold;
	}

	&__info-phone,

	&__info-email {
		font-size: 15px;
		line-height: 19px;
	}

	&__info-email:hover {
		text-decoration: underline;
	}

	&__form {
		width: 570px;
		margin-right: 106px;
	}

	&__form-title {
		font-size: 21px;
		line-height: 24px;
		margin-bottom: 13px;
		@extend %ub-bold;
	}

	&__form-text {
		font-size: 15px;
		line-height: 18px;
		margin-bottom: 12px;
	}

	&__form-line {
		display: flex;
		justify-content: space-between;
		margin-bottom: 10px;
	}

	&__form-input,
	&__form-textarea {

		background: rgba(0, 0, 0, 0.5);
		padding: 15px;
		border: none;
		font-size: 13px;
		line-height: 17px;
		@extend %ub-regular;
		color: rgba($color: #fff, $alpha: 0.4);

		&::placeholder {
			color: rgba($color: #fff, $alpha: 0.4);
		}

		&:focus {
			outline: none;
			border: none;
		}
	}

	&__form-input {
		width: 49%;
	}

	&__form-textarea {
		width: 100%;
		margin-bottom: 20px;
		height: 144px;
		resize: none;
	}

	&__form-btn {
		font-size: 14px;
		line-height: 16px;
		padding: 9px 15px;
		@extend %ub-bold;
		text-transform: uppercase;
		border: 1px solid #FFFFFF;
		border-radius: 2px;
		color: #fff;
		cursor: pointer;
		transition: $transition;

		&:hover {
			background-color: #fff;
			color: #1C1C1C;
		}
	}

	&__social {
		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	&__social-item+.contacts__social-item {
		margin-top: 25px;
	}

	&__social-link {
		opacity: 0.5;
		transition: $transition;

		&:hover {
			opacity: 1;
		}
	}
}

.footer {
	position: absolute;
	bottom: 32px;
	left: 0;
	right: 0;

	&__copy {
		font-size: 13px;
		line-height: 17px;
		opacity: 0.64;
		text-align: right;
	}
}