.header {
	position: fixed;
	left: 0;
	top: 30px;
	right: 0;
	z-index: 10;

	&__inner {
		display: flex;
		justify-content: space-between;
		align-items: center;
		min-height: 30px;
		text-transform: uppercase;
		font-size: 15px;
		line-height: 17px;
		@extend %ub-medium;
	}

	&__btn {
		color: #fff;
		padding: 6px 28px;
		background-color: transparent;
		border: 1px solid #fff;
		border-radius: 3px;
		margin-right: 8px;
		cursor: pointer;
		transition: $transition;
		&:hover {
			background-color: #fff;
			color: #1C1C1C;
		}
	}

	&__phone {
		@extend %ub-bold;
		font-size: 16px;
		line-height: 18px;
	}
}

.menu {
	&__list-item {
		display: inline-block;

		& + .menu__list-item {
			padding-left: 20px;
		}
	}
	&__list-phone {
		display: none;
	}
	&__btn {
		display: none;
	}
}