.question {
	&__items {
		max-width: 760px;
		padding-bottom: 15px;
	}


	&__item {
		background-color: rgba(0, 0, 0, 0.3);
		max-width: 690px;
		margin-bottom: 5px;
		position: relative;

		&:nth-child(3):before {
			position: absolute;
			content: '';
			width: 319px;
			height: 125px;
			top: -33px;
			right: -390px;
			background-image: url('../images/question-bg.png');
		}
	}


	&__item-title {
		font-size: 21px;
		line-height: 28px;
		@extend %ub-medium;
		color: rgba($color: #fff, $alpha: .8);
		padding: 20px 10px 24px 40px;
		position: relative;
		cursor: pointer;

		&::after {
			content: url('../images/accordion-arrow.svg');
			position: absolute;
			right: -72px;
			top: 0;
			width: 72px;
			height: 72px;
			background-color: #fff;
			display: flex;
			align-items: center;
			justify-content: center;
		}
	}

	&__item-text {
		font-size: 15px;
		line-height: 22px;
		padding: 0 5px 35px 50px;
		display: none;
	}

	&__item.question__item-active {
		.question__item-title::after {
			transform: rotate(180deg);
		}

		.question__item-text {
			display: block;
		}
	}
}