// GENERAL CSS SETTINGS 
html {
	scroll-behavior: smooth;
}

a {
	text-decoration: none;
	color: inherit;
}

body {
	font-family: $userfont;
	font-weight: 300;
	font-size: $regular-text;
	line-height: $lineheight;
	color: $text;
	min-width: 320px;
	overflow-x: hidden;
}

.wrapper {
	overflow: hidden;
	min-height: 100%;
	display: flex;
	flex-direction: column;
	position: relative;
}

.main {
	flex: 1 1 auto;
}

.container {
	max-width: $containerWidth;
	margin: 0px auto;
	padding: 0px $containerPadding;
	width: 100%;
}

.page-section {
	background-image: url('../images/page-bg.jpg');
	background-repeat: no-repeat;
	background-position: top center;
	background-size: cover;
	position: relative;

	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: linear-gradient(107.54deg, rgba(23, 26, 28, 0.72) 0%, #5E5E57 100%);
		opacity: 0.15;
	}

	&__wrapper {
		min-height: 100vh;
		z-index: 1;
		position: relative;
		padding: 74px 0 50px;
	}

}

.scroll__btn {
	padding: 13px 0 30px;
	text-align: center;
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
}

.title {
	@extend %sf-regular;
	font-size: 44px;
	line-height: 53px;
	padding-left: 25px;
	margin-bottom: 55px;
	margin-top: 150px;
}

.info {
	&__title {
		@extend %ub-bold;
		font-size: 21px;
		line-height: 28px;
		margin-bottom: 10px;
	}

}