// === DESKTOP FIRST === 

@media (max-width : 1366px) {
	body {
		font-size: 16px;
		line-height: 22px;
	}

	.header {
		background: rgba(#000, .6);
		top: 0;
		padding: 15px 0;
	}

	.top__title {
		font-size: 75px;
		line-height: 90px;
		margin-bottom: 20px;
	}

	.top__inner img {
		width: 80%;
		margin: 0px auto;
	}

	.top__text {
		margin: 15px auto 25px;
	}

	.title {
		margin-top: 10px;
	}

	.products__slider-box {
		padding-top: 0;
	}

	.products__slider-text {
		bottom: auto;
		top: -80px;
		left: auto;
		right: 0;
	}

	.benefits__item {
		margin-bottom: 80px;
	}

	.benefits__item:nth-child(2)::before {
		content: "";
		height: 45px;
		bottom: -45px;
	}

	.specification__box-2 {
		margin-top: 115px;
	}

	.specification__box-4 {
		margin-top: 115px;
	}


}

@media (max-width : 1280px) {
	.benefits__item {
		width: 48%;
	}

	.benefits__item:first-child::before {
		content: "";
		max-width: 35%;
		right: -60px;
	}

	.benefits__item:nth-child(2)::before {
		content: "";
		max-width: 95%;
		left: -420px;
	}

	.benefits__item:nth-child(3)::before {
		content: "";
		max-width: 35%;
		right: -35px;
		top: 50px;
	}

}

@media (max-width : 1100px) {
	.menu {
		order: 1;
	}

	.menu__list {
		position: fixed;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		background: rgba(#1c1c1c, .9);
		z-index: -1;
		display: flex;
		padding-top: 90px;
		padding-left: 24px;
		flex-direction: column;
		justify-content: space-around;
		transform: translateY(-120%);
		transition: $transition;
		overflow: auto;


		&--active {
			transform: translateY(0);
		}
	}

	.menu__list-item+.menu__list-item {
		padding-left: 0;
	}

	.menu__list-item {
		margin-bottom: 35px;

		&:first-child {
			margin-top: auto;
		}
	}

	.menu__list-phone,
	.menu__list-link {
		display: block;
	}

	.menu__list-phone {
		padding-top: 33px;
		padding-bottom: 20px;
		position: relative;
		margin-top: auto;
		margin-bottom: auto;
		font-weight: 700;
		font-size: 16px;
		line-height: 18px;

		&::before {
			content: '';
			position: absolute;
			border-top: 1px solid rgba(#fff, .24);
			top: 0;
			left: -24px;
			right: 0;

		}
	}

	.menu__btn {
		display: block;
		background-color: transparent;
		border: none;
		outline: none;
		padding: 0;
		cursor: pointer;

		&:focus {
			outline: none;
		}
	}

	.menu__btn::after,
	.menu__btn::before {
		content: '';
		width: 24px;
		height: 2px;
		background: #E0E0E0;
		display: block;
		margin-bottom: 4px;
		transition: $transition;
	}

	.menu__btn::after {
		margin-bottom: 0;
		width: 16px;
		margin-left: auto;
	}

	.menu__btn-line {
		background: #E0E0E0;
		display: block;
		width: 24px;
		height: 2px;
		margin-bottom: 4px;
		opacity: 1;
		transition: $transition;
	}

	.menu__btn.menu__btn--active {
		.menu__btn-line {
			opacity: 0;
			transition: opacity .1s;
		}

		&::after {
			width: 24px;
			transform: translateY(-6px) rotate(-45deg);
		}

		&::before {
			transform: translateY(6px) rotate(45deg);
		}
	}

	.header__phone {
		display: none;
	}

	.header__btn {
		margin-right: 0;
	}

	.products__text {
		padding-right: 15px;
	}

	.products__slider-box {
		padding-right: 20px;
	}

	.benefits__item::before {
		display: none;
	}

	.benefits__items {
		display: block;
	}

	.benefits__item {
		width: 100%;
		margin-bottom: 40px;
	}

	.benefits__item:nth-child(even) {

		transform: translateY(0);
		margin-left: 200px;
	}

	.benefits__info {
		width: 35%;
	}

	.specification__item {
		& img {
			width: 100%;
		}
	}

	.specification {
		.title {
			padding-top: 40px;
		}
	}

	.specification__box-1::before,
	.specification__box-2::before,
	.specification__box-3::before,
	.specification__box-4::before {
		display: none;
	}

	.question__item-title {
		line-height: 22px;
		font-size: 18px;
		padding: 16px 10px 16px 30px;
	}

	.question__item-title::after {
		right: -56px;
		width: 54px;
		height: 54px;
	}
}



@media (max-width: 1024px) {
	.title {
		margin-top: 0px;
		font-size: 35px;
		line-height: 42px;
		margin-bottom: 35px;
	}

	.products__slider-text {
		font-size: 20px;
		line-height: 22px;
		top: -60px;
	}


}

@media (max-width: 940px) {
	.products__inner {
		display: block;
	}

	.products__text {
		padding-right: 0;
		max-width: 100%;
		margin-bottom: 90px;
	}

	.products__slider {
		margin-left: auto;
	}

	.products__slider-box {
		padding-bottom: 50px;
	}

	.contacts__inner {
		flex-wrap: wrap;
	}

	.contacts__info {
		width: 100%;
		display: flex;
		margin-right: 0;
	}

	.contacts__form {
		width: 100%;
		margin-left: 50px;
		margin-right: 0;
	}

	.contacts__info,
	.contacts__form {
		margin-left: 0;
	}

	.contacts__social {
		margin: 0 auto;
	}

	.contacts__social-list {
		display: flex;
		align-items: flex-end;
		gap: 25px;
	}

}

@media (max-width: 877px) {

	.specification__box-2,
	.specification__box-4 {
		margin-top: 50px;
	}
}

@media (max-width: 787px) {
	.benefits__item:nth-child(even) {
		margin-left: 0;
	}

	.benefits__items {
		padding-left: 0;
	}

	.benefits__item:nth-child(3) {
		transform: translateX(0);
	}

	.benefits__info {
		width: 100%;
	}

	.specification__inner {
		padding-left: 0;
		display: block;
	}

	.specification__box {
		max-width: 100%;
	}

	.specification__box-2,
	.specification__box-4 {
		margin-top: 25px;
	}

	.question__items {
		padding-right: 54px;
	}
}

@media (max-width : 680px) {
	.title {
		padding-left: 0;
		margin-bottom: 23px;
		font-size: 26px;
		line-height: 28px;
	}

	.products__inner {
		padding-left: 0;
	}

	.products__text p {
		margin-bottom: 10px;
	}

	.products__slider {
		max-width: 100%;

		&::before {
			width: 100%;
			height: 100%;
		}

		&-img {
			max-width: 100%;
			max-height: 100%;
		}
	}

	.benefits__num {
		display: none;
	}

	.info__title {
		font-size: 20px;
		line-height: 22px;
		margin-top: 10px;
	}

	.contacts__info-title {
		margin-bottom: 6px;
		font-size: 16px;
		line-height: 18px;
	}

	.contacts__info-email,
	.contacts__info-phone {
		font-size: 14px;
		line-height: 16px;
		display: block;
	}

	.contacts__info {
		flex-wrap: wrap;
	}
}

@media (max-width : 540px) {
	.top__wrapper {
		justify-content: space-between;
	}

	.top__title {
		display: none;
	}

	.top__text {
		flex: 1 1 auto;
	}

	.scroll__btn {
		padding: 13px 0;
	}

	.benefits__item {
		display: block;
	}

	.question__item-title {
		line-height: 20px;
		font-size: 16px;
		padding: 10px 50px 10px 10px;
	}

	.question__item-text {
		font-size: 14px;
		line-height: 18px;
		padding: 5px 25px 15px 30px;
	}

	.question__item-title::after {
		right: 0;
		width: 40px;
		height: 40px;
	}

	.question__items {
		padding-right: 0;
	}
}

@media (max-height: 375px) {
	.menu__list-phone {
		padding: 15px 0;
	}
}

@media (max-height: 340px) {
	.menu__list-item {
		margin-bottom: 18px;
	}
}