.specification {
	&__inner {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-top: 80px;
		padding-left: 50px;
	}

	&__box {
		max-width: 280px;
		width: 100%;
		position: relative;
		margin-top: 10px;

		&::before {
			content: '';
			position: absolute;
			background-repeat: no-repeat;
			background-position: 0 0;
		}
	}

	&__box-2 {
		margin-top: 140px;
	}

	&__box-4 {
		margin-top: 160px;
	}

	&__box-1::before {
		background-image: url('../images/pointer-1.svg');
		width: 235px;
		height: 118px;
		right: -190px;
		top: 15px;
	}


	&__box-2::before {
		background-image: url('../images/pointer-2.svg');
		width: 346px;
		height: 85px;
		right: -320px;
		top: -65px;
	}

	&__box-3::before {
		background-image: url('../images/pointer-3.svg');
		width: 265px;
		height: 108px;
		left: -300px;
		top: 15px;
	}

	&__box-4::before {
		background-image: url('../images/pointer-4.svg');
		width: 129px;
		height: 94px;
		left: -160px;
		top: -79px;
	}

	&__title {
		font-size: 21px;
		line-height: 28px;
		@extend %ub-bold;
		margin-bottom: 10px;
	}

	&__text {
		font-size: 15px;
		line-height: 18px;
	}
}