html {
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
  color: inherit;
}

body {
  font-family: UbuntuLight, sans-serif;
  font-weight: 300;
  font-size: 17px;
  line-height: 26px;
  color: #fff;
  min-width: 320px;
  overflow-x: hidden;
}

.wrapper {
  overflow: hidden;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.main {
  flex: 1 1 auto;
}

.container {
  max-width: 1240px;
  margin: 0px auto;
  padding: 0px 15px;
  width: 100%;
}

.page-section {
  background-image: url("../images/page-bg.jpg");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  position: relative;
}
.page-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(107.54deg, rgba(23, 26, 28, 0.72) 0%, #5E5E57 100%);
  opacity: 0.15;
}
.page-section__wrapper {
  min-height: 100vh;
  z-index: 1;
  position: relative;
  padding: 74px 0 50px;
}

.scroll__btn {
  padding: 13px 0 30px;
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}

.title {
  font-size: 44px;
  line-height: 53px;
  padding-left: 25px;
  margin-bottom: 55px;
  margin-top: 150px;
}

.info__title {
  font-size: 21px;
  line-height: 28px;
  margin-bottom: 10px;
}

.header {
  position: fixed;
  left: 0;
  top: 30px;
  right: 0;
  z-index: 10;
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 30px;
  text-transform: uppercase;
  font-size: 15px;
  line-height: 17px;
}
.header__btn {
  color: #fff;
  padding: 6px 28px;
  background-color: transparent;
  border: 1px solid #fff;
  border-radius: 3px;
  margin-right: 8px;
  cursor: pointer;
  transition: 0.5s cubic-bezier(0.42, 0.61, 0.58, 0.41);
}
.header__btn:hover {
  background-color: #fff;
  color: #1C1C1C;
}
.header__phone {
  font-size: 16px;
  line-height: 18px;
}

.menu__list-item {
  display: inline-block;
}
.menu__list-item + .menu__list-item {
  padding-left: 20px;
}
.menu__list-phone {
  display: none;
}
.menu__btn {
  display: none;
}

.top {
  background-image: url("../images/top-bg.jpg");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
}
.top::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(45.97% 54.43% at 50% 45.57%, rgba(23, 26, 28, 0) 50.28%, rgba(23, 26, 28, 0.00225974) 50.29%, rgba(4, 5, 5, 0.820343) 58.72%, rgba(5, 5, 6, 0.792703) 80.08%, #000000 87.89%);
  opacity: 0.4;
}
.top__wrapper {
  display: flex;
  align-items: center;
}
.top__inner {
  display: flex;
  flex-direction: column;
  text-align: center;
  height: 100%;
  justify-content: flex-end;
  padding-bottom: 20px;
}
.top__title {
  font-size: 96px;
  line-height: 115px;
  margin-bottom: 25px;
}
.top__text {
  margin: 25px auto 35px;
  max-width: 325px;
}
.top__social-list {
  display: flex;
  justify-content: center;
  gap: 50px;
}
.top__social-link {
  opacity: 0.5;
  transition: 0.5s cubic-bezier(0.42, 0.61, 0.58, 0.41);
}
.top__social-link:hover {
  opacity: 1;
}

.products__inner {
  display: flex;
  justify-content: space-between;
  padding-left: 50px;
}
.products__text {
  max-width: 415px;
}
.products__text p {
  margin-bottom: 30px;
}
.products__slider-box {
  position: relative;
  padding-top: 110px;
}
.products__slider-text {
  width: 285px;
  position: absolute;
  text-align: right;
  font-size: 24px;
  line-height: 28px;
  bottom: 30px;
  left: -250px;
  z-index: 3;
}
.products__slider {
  max-width: 560px;
}
.products__slider::before {
  content: "";
  position: absolute;
  width: 560px;
  height: 276px;
  border: 4px solid #fff;
  top: -15px;
  right: -17px;
}
.products__slider-img {
  max-width: 560px;
  max-height: 270px;
  width: 100%;
  height: 100%;
}

.slider-btn {
  padding: 16px 26px;
  border: none;
  background-color: #fff;
  position: absolute;
  bottom: -59px;
  right: 0;
  cursor: pointer;
  z-index: 5;
}
.slider-btn:focus {
  outline: none;
}
.slider-btn svg {
  stroke: #000;
}
.slider-btn__left {
  right: 60px;
}
.slider-btn.slick-disabled {
  background-color: #000;
}
.slider-btn.slick-disabled svg {
  stroke: #fff;
}

.benefits__items {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-left: 25px;
}
.benefits__item {
  margin-bottom: 100px;
  display: flex;
  align-items: flex-end;
}
.benefits__item:nth-child(even) {
  transform: translateY(25px);
  margin-left: 40px;
}
.benefits__item:nth-child(3), .benefits__item:nth-child(4) {
  transform: translateX(20px);
}
.benefits__item:nth-child(4) {
  transform: translateY(25px);
}
.benefits__item:first-child, .benefits__item:nth-child(2), .benefits__item:nth-child(3) {
  position: relative;
}
.benefits__item:first-child::before, .benefits__item:nth-child(2)::before, .benefits__item:nth-child(3)::before {
  position: absolute;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
}
.benefits__item:first-child::before {
  content: "";
  height: 50px;
  max-width: 265px;
  background-image: url("../images/line-1.svg");
  right: -75px;
  top: 32px;
}
.benefits__item:nth-child(2)::before {
  content: "";
  height: 65px;
  max-width: 525px;
  background-image: url(../images/line-2.svg);
  left: -490px;
  bottom: -70px;
}
.benefits__item:nth-child(3)::before {
  content: "";
  height: 47px;
  max-width: 275px;
  background-image: url(../images/line-3.svg);
  right: -80px;
  top: 40px;
}
.benefits__num {
  font-size: 36px;
  line-height: 41px;
  margin-right: 10px;
}
.benefits__img {
  max-width: 195px;
  max-height: 130px;
  width: 100%;
  height: 100%;
  position: relative;
  margin-right: 32px;
}
.benefits__img::before {
  content: "";
  position: absolute;
  width: 191px;
  height: 126px;
  border: 2px solid #fff;
  top: -12px;
  right: -12px;
  z-index: -1;
}

.info {
  width: 300px;
}
.info__title {
  width: 123px;
  font-size: 21px;
  line-height: 24px;
}
.info__text {
  width: 100%;
  font-size: 15px;
  line-height: 130%;
}

.specification__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 80px;
  padding-left: 50px;
}
.specification__box {
  max-width: 280px;
  width: 100%;
  position: relative;
  margin-top: 10px;
}
.specification__box::before {
  content: "";
  position: absolute;
  background-repeat: no-repeat;
  background-position: 0 0;
}
.specification__box-2 {
  margin-top: 140px;
}
.specification__box-4 {
  margin-top: 160px;
}
.specification__box-1::before {
  background-image: url("../images/pointer-1.svg");
  width: 235px;
  height: 118px;
  right: -190px;
  top: 15px;
}
.specification__box-2::before {
  background-image: url("../images/pointer-2.svg");
  width: 346px;
  height: 85px;
  right: -320px;
  top: -65px;
}
.specification__box-3::before {
  background-image: url("../images/pointer-3.svg");
  width: 265px;
  height: 108px;
  left: -300px;
  top: 15px;
}
.specification__box-4::before {
  background-image: url("../images/pointer-4.svg");
  width: 129px;
  height: 94px;
  left: -160px;
  top: -79px;
}
.specification__title {
  font-size: 21px;
  line-height: 28px;
  margin-bottom: 10px;
}
.specification__text {
  font-size: 15px;
  line-height: 18px;
}

.question__items {
  max-width: 760px;
  padding-bottom: 15px;
}
.question__item {
  background-color: rgba(0, 0, 0, 0.3);
  max-width: 690px;
  margin-bottom: 5px;
  position: relative;
}
.question__item:nth-child(3):before {
  position: absolute;
  content: "";
  width: 319px;
  height: 125px;
  top: -33px;
  right: -390px;
  background-image: url("../images/question-bg.png");
}
.question__item-title {
  font-size: 21px;
  line-height: 28px;
  color: rgba(255, 255, 255, 0.8);
  padding: 20px 10px 24px 40px;
  position: relative;
  cursor: pointer;
}
.question__item-title::after {
  content: url("../images/accordion-arrow.svg");
  position: absolute;
  right: -72px;
  top: 0;
  width: 72px;
  height: 72px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}
.question__item-text {
  font-size: 15px;
  line-height: 22px;
  padding: 0 5px 35px 50px;
  display: none;
}
.question__item.question__item-active .question__item-title::after {
  transform: rotate(180deg);
}
.question__item.question__item-active .question__item-text {
  display: block;
}

.contacts__inner {
  display: flex;
}
.contacts__info {
  width: 240px;
  margin-right: 60px;
  margin-left: 50px;
}
.contacts__info-box {
  margin-bottom: 30px;
  margin-right: 15px;
}
.contacts__info-title {
  margin-bottom: 13px;
  font-size: 21px;
  line-height: 24px;
}
.contacts__info-phone, .contacts__info-email {
  font-size: 15px;
  line-height: 19px;
}
.contacts__info-email:hover {
  text-decoration: underline;
}
.contacts__form {
  width: 570px;
  margin-right: 106px;
}
.contacts__form-title {
  font-size: 21px;
  line-height: 24px;
  margin-bottom: 13px;
}
.contacts__form-text {
  font-size: 15px;
  line-height: 18px;
  margin-bottom: 12px;
}
.contacts__form-line {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.contacts__form-input, .contacts__form-textarea {
  background: rgba(0, 0, 0, 0.5);
  padding: 15px;
  border: none;
  font-size: 13px;
  line-height: 17px;
  color: rgba(255, 255, 255, 0.4);
}
.contacts__form-input::placeholder, .contacts__form-textarea::placeholder {
  color: rgba(255, 255, 255, 0.4);
}
.contacts__form-input:focus, .contacts__form-textarea:focus {
  outline: none;
  border: none;
}
.contacts__form-input {
  width: 49%;
}
.contacts__form-textarea {
  width: 100%;
  margin-bottom: 20px;
  height: 144px;
  resize: none;
}
.contacts__form-btn {
  font-size: 14px;
  line-height: 16px;
  padding: 9px 15px;
  text-transform: uppercase;
  border: 1px solid #FFFFFF;
  border-radius: 2px;
  color: #fff;
  cursor: pointer;
  transition: 0.5s cubic-bezier(0.42, 0.61, 0.58, 0.41);
}
.contacts__form-btn:hover {
  background-color: #fff;
  color: #1C1C1C;
}
.contacts__social {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.contacts__social-item + .contacts__social-item {
  margin-top: 25px;
}
