.benefits {
	&__items {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		padding-left: 25px;
	}

	&__item {
		margin-bottom: 100px;
		display: flex;
		align-items: flex-end;

		&:nth-child(even) {
			transform: translateY(25px);
			margin-left: 40px;
		}

		&:nth-child(3),
		&:nth-child(4) {
			transform: translateX(20px);
		}

		&:nth-child(4) {
			transform: translateY(25px);
		}

		&:first-child,
		&:nth-child(2),
		&:nth-child(3) {
			position: relative;

			&::before {
				position: absolute;
				width: 100%;
				background-repeat: no-repeat;
				background-size: cover;
				background-position: 0 0;
			}
		}


		&:first-child {

			&::before {
				content: '';
				height: 50px;
				max-width: 265px;
				background-image: url('../images/line-1.svg');
				right: -75px;
				top: 32px;
			}
		}

		&:nth-child(2) {

			&::before {
				content: '';
				height: 65px;
				max-width: 525px;
				background-image: url(../images/line-2.svg);
				left: -490px;
				bottom: -70px;
			}
		}

		&:nth-child(3) {

			&::before {
				content: '';
				height: 47px;
				max-width: 275px;
				background-image: url(../images/line-3.svg);
				right: -80px;
				top: 40px;
			}
		}
	}


	&__num {
		font-size: 36px;
		line-height: 41px;
		@extend %ub-bold;
		margin-right: 10px;
	}

	&__img {
		max-width: 195px;
		max-height: 130px;
		width: 100%;
		height: 100%;
		position: relative;
		margin-right: 32px;

		&::before {
			content: '';
			position: absolute;
			width: 191px;
			height: 126px;
			border: 2px solid #fff;
			top: -12px;
			right: -12px;
			z-index: -1;
		}
	}
}

.info {
	width: 300px;

	&__title {
		width: 123px;
		font-size: 21px;
		line-height: 24px;
	}

	&__text {
		width: 100%;
		font-size: 15px;
		line-height: 130%;
		@extend %ub-light;
	}
}